#dashboard {
  display: grid;
  grid-template-columns: 62vw 20vw;
  grid-auto-flow: column;
}

.dashboard-left {
  width: 100%;
  height: 100%;
  padding: 2rem;
  color: var( --color-navbar-background);
}


.create-btn {
  height: 3.75rem;
  width: 10.125rem;
  border-radius: 2.5rem;
  background-color: var(--color-navbar-background);
  color: var(--color-white);
  position: relative;
  cursor: pointer;
}

.create-btn > span {
  padding: 0.3rem;
}

/* STUDENTS, TEACHERS, CENTRE CARDS */
#cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-auto-flow: column;
  gap: 1rem;
  padding: 2rem 0 2rem 0;
}

#cards .stc {
  padding: inherit;
  background-color: var(--color-white);
  border-radius: var(--card-border);
}


.stc .left {
  height: 5rem;
  width: 5rem;
  background-color: var(--color-background);
  text-align: center;
  border-radius: 100%;
  margin-left: 2.2rem;
}
.stc .right {
  height: 4rem;
  width: 4rem;
  margin-left: 1.5rem;
}

.stc-icons {
  width: 3rem;
  height: 3rem;
}
.right .title {
  display: block;
  color: var(--color-muted-test);
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 1.2rem;
  text-transform: capitalize;
}
.right .total {
  color: var(--color-heading);
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 2rem;
}

/* ATTENDANCE */

#attendance-section {
  background-color: var(--color-white);
  width: 100%;
  height: 30rem;
  color: var(--color-heading);
  border-radius: var(--card-border);
  padding: 2rem;
}

.circle {
  border-radius: 50%;
  display: inline;

  position: relative;
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 10px;
  padding: 0 0.5rem 0 0.5rem;
}

.bg-color-1{
  background: var(--color-present);
}
.bg-color-2{
  background: var(--color-absent);
}
.bg-color-3{
  background: var(--color-leave);
}
.p-a-l > span{
  padding: 0 0.5rem 0 0.5rem;
  color: var(--color-muted-test);
}

.filters-btn{
  width: 7.5rem;
  height: 2.5rem;
  border: 0.1rem solid var(--color-heading);
  border-radius: 1.4rem;
  cursor: pointer;
}
.filters-btn span{
  margin-left: 0.5rem;
  font-size: 1.2rem;
  text-transform: capitalize;
}


/* RECENTLY JOINED STUDENTS */

#recently-joined-students{
  width: 100%;
  height: 48rem;
  padding: 2rem;
  background-color: var(--color-white);
  margin-top: 2rem;
  border-radius: var(--card-border);
}

#recently-joined-students #studentprofile{
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}

table{
  padding: 1rem 0 0 0;
  width: 100%;
  text-align: center;
  margin: auto;
  text-transform: capitalize;
}
tr{
  height: 5rem;
  width: 100%;
}


/* DASHBORD RIGHT ASIDE SECTION */


#dashboard-right {
  background-color: var(--color-white);
  width: 100%;
  height: 100%;
  padding: 2rem;
}

/* User Profile */
/* 100% rule will only apply to dashboard page */
#dashboard-right #user-profile-div{
  width: 100%;
  height: 100%;
  padding-right: 1rem;
  justify-content: space-around;
}
/* display none rule will only apply to dashboard page */
#dashboard-right .profile-setting{
  display: none;
}

/* Recently Joined Teachers */

.recently-joined-teachers {
  padding: 2rem 4rem 2rem 0;
}

.teachers-div{
  padding: 0.5rem 0 0.5rem 0;
  width: 100%;
}

.teachers-details{
  padding: 0.5rem;
}

.teachers-profile #teacherprofile{
  width: 3rem;
  height: 3rem;
}
.teachers-name,
.teachers-centre{
  display: block;
  padding: 0 0 0 0.9rem;
}
.teachers-name{
  font-weight: 500;
  font-size: 0.8rem;
}
.teachers-centre{
  font-family: 'Poppins';
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1.2rem;
  color: var(--color-muted-test);
  text-transform: capitalize;
}
.line{
  width: 100%;
  border: 0.01rem solid var(--color-muted-test);
  padding-right: 2rem;
}

.view-more-btn{
  width: 100%;
  height: 3rem;
  margin: 1rem 0 0 0;
  padding: 1rem;
  background-color: var(--color-background);
  font-size: 1rem;
  border-radius: 2rem;
  cursor: pointer;
}













/* Large desktop screens (1440px and above) */
@media (min-width: 1440px) {
  #dashboard {
    display: grid;
    grid-template-columns: 62vw 20vw;
    grid-auto-flow: column;
  }

}

/* Medium-sized desktop screens (1280px to 1439px) */
@media (min-width: 1280px) and (max-width: 1439px) {
  #dashboard {
    display: grid;
    grid-template-columns: 60vw 23vw;
    grid-auto-flow: column;

  }
  .recently-joined-teachers .poppins-sub-heading{
    font-size: 1rem;
  }

}
/* Small desktop screens and tablets (1024px to 1279px) */
@media (min-width: 1024px) and (max-width: 1279px) {
 
  #dashboard {
    display: grid;
    grid-template-columns: 61vw 24vw;
    grid-auto-flow: column;

  }
  .recently-joined-teachers .poppins-sub-heading{
    font-size: 1rem;
  }

  
}


/* Tablets (768px to 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
 
  #dashboard {
    display: grid;
    grid-template-columns: 100%;
    grid-auto-flow: column;
  }
  #dashboard-right{
    display: none;
  }
  #cards {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: column;
  }


}

/* Mobile devices in landscape orientation (480px to 767px) */
@media (min-width: 480px) and (max-width: 767px) {
  #dashboard {
    display: grid;
    grid-template-columns: 100%;
    grid-auto-flow: column;

  }
  #dashboard-right{
    display: none;
  }
  #cards {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: column;
  }
  #recently-joined-students table{
    font-size: 0.6rem;
  }
}

/* Mobile devices in portrait orientation (320px to 479px) */
@media (min-width: 320px) and (max-width: 479px) {

  #dashboard {
    display: grid;
    grid-template-columns: 100%;
    grid-auto-flow: column;

  }
  #dashboard-right{
    display: none;
  }
  #cards {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: column;
  }
  #recently-joined-students table{
    font-size: 0.4rem;
  }


}
